import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/home/Home.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    children: [{
        path: 'hoting',
        component: () => import('@/components/Content/Hoting.vue')
      },
      {
        path: 'coming',
        component: () => import('@/components/Content/Coming.vue')
      }
    ]
  },{
    path:'/detail/:id',
    namae:'detail',
    component:() => import('@/views/home/childComps/MovieDetail.vue')
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  // mode: 'history',
  mode:'hash',
  // base: process.env.BASE_URL,
  base:'/maoyan/',
  routes
})

export default router