<template>
   <div class="tab-control">

    <div v-for="(item,index) in titleList"
         :key="index"
         @click="itemClick(index)"
         class="tab-control-item"
         :class="{active:index == currentIndex}">
      <router-link :to="item.url" tag="span">
        {{ item.title }}
      </router-link>
    </div>
  </div>
</template>

<script>

export default {
  name: 'TabControl',
  props: {
    titleList: {
      type: Array,
      default(){
        // "正在热映", "即将上映"
        return [];
      }
    }
  },
  data () {
    return {
      currentIndex: 0
    };
  },

  components: {},

  computed: {},

  methods: {
    itemClick (index) {
      this.currentIndex = index;
    }
  },

  created(){
    // 默认显示第一个页面
    //console.log(this.$route.path.indexOf(this.titleList[0].url));
    if(this.$route.path.indexOf(this.titleList[0].url) === -1){
      this.$router.replace(this.titleList[0].url);
    }
  }
}

</script>
<style scoped>

  .tab-control{
    height: 44px;
    line-height: 44px;

    display: flex;
    text-align: center;
    border-bottom: 1px solid #e6e6e6;
  }

  .tab-control-item{
    flex: 1;
  }

  .tab-control-item span{
    padding: 4px 4px 10px;
    font-size: 16px;
  }

  div.active span{
    color: var(--color-tint);
    border-bottom: 2px solid var(--color-high-text)
  }

</style>