<template>
  <div>
    <div v-if="isShow"
         class="loading-bg">
      <img class="loading"
           src="@/assets/img/loading.gif"
           alt="">
      <p>{{ msg }}</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Loading',
  props: [''],
  data () {
    return {
      msg: "",
      isShow: false
    };
  },

  components: {},

  computed: {},

  methods: {
    // 显示进度条
    showLoading (msg = "正在加载...") {
      this.msg = msg;
      this.isShow = true;
      // console.log("显示进度条")
    },
    // 关闭进度条
    closeLoading () {
      // this.msg = "";
      this.isShow = false;
      // console.log("隐藏进度条")
    }
  },

}

</script>
<style lang='scss' scoped>
.loading-bg {
  text-align: center;
  position: fixed;
  padding: 20px;
  background-color: rgba(32, 32, 32, 0.493);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 8px;
  z-index: 9;
  > .loading {
    width: 32px;
    height: 32px;
  }
  > p {
    margin-top: 20px;
    color: #fff;
  }
}
</style>
