function comingList() {
  let res = {
    coming: []
  };
  for (let i = 0; i < 10; i++) {
    res.coming.push({
      "id": Random.id(),
      "img": Random.dataImage('128x180'),
      "wish": Random.integer(100, 100000),
      "star": Random.ctitle(),
      "showInfo": '今天暂无场次',
      "preShow": true,
      "nm": Random.ctitle()
    })
  }

  return res;
}

